import React from 'react';
import './ImageLinkForm.css'

const ImageLinkForm = ({inputChange, detectImage}) => {
	return(
		<div className="f3">
		<p>Hi, I will use my brain and try to detect faces in the image link you provided</p>
			<div className="center">
				<div style={{width:'700px'}} className="center f4 pa4 br3 shadow-5 honey">
					<input  className="pa2 br2 w-60" type="text" onChange={inputChange}/>
					<button className="grow  br2 link ph3 pv2 dib white bg-light-purple w-30"
							onClick={detectImage}>Detect
					</button>
				</div>
			</div>
		</div>
	);
}

export default ImageLinkForm;


