import React from 'react';

const Navigation = ({isSignedin, onRouteChange}) => {
		return(
			<nav style={{display:'flex',flexDirection:'col',justifyContent:'flex-end'}}>
				{(isSignedin)
				 ? <p onClick={() => onRouteChange('signin')} className="f3 pointer link dim black pa3 underline dib" >Sign Out</p>
				 : <div>
					   <p onClick={() => onRouteChange('register')} className="f3 pointer link dim black pa3 underline dib" >Register</p>
					   <p onClick={() => onRouteChange('signin')} className="f3 pointer link dim black pa3 underline dib" >Sign in</p>
				   </div>
				}	
				
			</nav>
		);
	
}

export default Navigation;


