import React from 'react';
import Tilt from 'react-tilt';
import Brain from './brain.png';
import './Logo.css';

const Logo = () => {
	return(
		<div className="ma4 mt0">
			<Tilt className="Tilt br3  pa3 shadow-3 " options={{ max : 50 }} style={{ height: 170, width: 170}} >
				 <div className="Tilt-inner"><img alt="Logo" style={{paddingTop: '5px'}} src={Brain}/> </div>
			</Tilt>
		</div>
	);
}

export default Logo;


