import React, { Component } from 'react';
import Navigation from './components/Navigation/Navigation';
import './App.css';
import Logo from './components/Logo/Logo';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import ImageRecognition from './components/ImageRecognition/ImageRecognition';
import Signin from './components/Signin/Signin';
import Register from './components/Register/Register';
import Rank from './components/Rank/Rank';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';

const app = new Clarifai.App({
 apiKey: 'b304a81edc92484a8c6bc0e680f97a7f'
});

const particleOptions = {
                particles:{
                  number:{
                    value: 30,
                    density:{
                      enable: true,
                      value_area:800
                    }
                  },
                  "move": {
                  "enable": true,
                  "speed": 6,
                   }
                }
};

class App extends Component {
  constructor(){
    super();
    this.state = {
      input : '',
      imageUrl: '',
      box: [],
      route: 'register',
      isSignedin: false,
    }
  }

  calculateFaceLocation = (response) => {
    const regionsArray = response.outputs[0].data.regions;
    const claraifaiFaceArray = regionsArray.map(region =>  region.region_info.bounding_box)
    const image = document.getElementById("inputImage");
    const width = Number(image.width);
    const height = Number(image.height);
    const boxArray = claraifaiFaceArray.map(claraifaiFace => {
      return {
      topRow: claraifaiFace.top_row * height,
      bottomRow: height - (claraifaiFace.bottom_row * height),
      rightCol: width - (claraifaiFace.right_col * width),
      leftCol: claraifaiFace.left_col * width
      }
    })
    return boxArray;
  }

  displayFaceBox = (box) =>{
    this.setState({box: box});
  }

  inputChange = (event) => {
    this.setState({input:event.target.value});  
  }

  onRouteChange = (page) => {
    if(page === 'home'){
       this.setState({isSignedin : true})
    }else if(page === 'signin' || page=== 'register' ){
       this.setState({isSignedin : false})
    }
    this.setState({route : page})
  }

  detectImage = () => {
    this.setState({imageUrl:this.state.input});
    app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.input)
    .then(response => {this.displayFaceBox(this.calculateFaceLocation(response))})
    .catch(err => console.log(err));
  }

  render() {
    const {isSignedin, box, imageUrl, route } = this.state;
    return (
      <div className="App">
        <Particles className="particles" 
         params={particleOptions}
        />
        <Navigation route={isSignedin} onRouteChange={this.onRouteChange}  />
        {(route === 'home')
          ? <div>
              <Logo />
              <Rank />
              <ImageLinkForm  inputChange={this.inputChange} detectImage={this.detectImage}/>
              <ImageRecognition box={box} imageUrl={imageUrl}/>
            </div>
          : (this.state.route === 'signin') 
            ? <Signin onRouteChange={this.onRouteChange} />
            : <Register onRouteChange={this.onRouteChange} /> 
          } 
      </div>         
    );
  }
}

export default App;



