import React from 'react';
import './ImageRecognition.css'

const ImageRecognition = ({imageUrl, box}) => {
	console.log("box received in ImageRecognition component is ", box)
	const displayBoxes = box.map((eachBox,i) => {
		return 	<div key={i} className='bounding-box' style={{top:eachBox.topRow, bottom: eachBox.bottomRow, left:eachBox.leftCol, right:eachBox.rightCol}}></div>
	})
	return(
		<div className="center ma">
			<div className="absolute mt2">
				<img alt="inputImage" id = "inputImage" src = {imageUrl} width='500px' height='auto'/>
				{displayBoxes}
			</div>
		</div>
	);
}

export default ImageRecognition;


